import { promoTiles } from "@/data/migration-content";
import Link from "next/link";
import { Button } from "@/components/ui/button";

export const PromoGrid = () => {
  return (
    <section className="py-16">
      <div className="container px-4">
        <div className="grid gap-6 md:grid-cols-3">
          {promoTiles.map((tile) => (
            <article
              key={tile.id}
              className="rounded-3xl overflow-hidden border border-border bg-white group flex flex-col transition-transform duration-500 hover:-translate-y-2 hover:shadow-2xl"
            >
              <div
                className="p-8 flex flex-col gap-4"
                style={{
                  background: tile.accent.background,
                }}
              >
                <span
                  className="inline-flex text-xs uppercase tracking-[0.2em] px-3 py-1 rounded-full w-fit"
                  style={{
                    backgroundColor: tile.accent.badge,
                    color: tile.accent.badge === "#111111" ? "#ffffff" : "#111111",
                  }}
                >
                  {tile.badge}
                </span>
                <h3 className="text-2xl font-semibold text-foreground">{tile.title}</h3>
                <p className="text-lg text-muted-foreground">{tile.subtitle}</p>
                <Button asChild variant="outline" className="w-fit bg-white text-foreground hover:bg-foreground hover:text-white">
                  <Link href="/shop">Shop Now</Link>
                </Button>
              </div>
              <div className="flex-1 overflow-hidden">
                <img src={tile.image} alt={tile.title} className="w-full h-64 object-cover transition-transform duration-700 group-hover:scale-110" loading="lazy" />
              </div>
            </article>
          ))}
        </div>
      </div>
    </section>
  );
};

