"use client";

import { brandSpotlight } from "@/data/migration-content";
import { Carousel, CarouselContent, CarouselItem } from "@/components/ui/carousel";

export const BrandSpotlight = () => {
  return (
    <section className="bg-muted/40 py-16">
      <div className="container px-4">
        <header className="mb-8 flex flex-col gap-2">
          <p className="text-sm uppercase tracking-[0.3em] text-muted-foreground">Design Partners</p>
          <h2 className="text-3xl font-semibold text-foreground">Trusted by Global Heritage Brands</h2>
        </header>

        <Carousel
          opts={{
            align: "start",
            loop: true,
          }}
        >
          <CarouselContent>
            {brandSpotlight.map((brand) => (
              <CarouselItem
                key={brand.id}
                className="basis-full sm:basis-1/2 lg:basis-1/3 xl:basis-1/4"
              >
                <article className="bg-white rounded-2xl border border-border/70 p-6 h-full flex flex-col justify-between hover:shadow-xl hover:-translate-y-1 transition-all duration-500 group">
                  <div className="flex items-center gap-4">
                    <div className="h-16 w-16 rounded-xl bg-muted flex items-center justify-center overflow-hidden">
                      <img src={brand.logo} alt={brand.name} className="max-h-12 object-contain transition-transform duration-500 group-hover:scale-105" />
                    </div>
                    <div>
                      <p className="text-lg font-semibold text-foreground">{brand.name}</p>
                      <p className="text-sm text-muted-foreground">{brand.tagline}</p>
                    </div>
                  </div>
                  <div className="mt-8 text-sm text-muted-foreground">
                    Certified supply chain · Seamless co-branded launches
                  </div>
                </article>
              </CarouselItem>
            ))}
          </CarouselContent>
        </Carousel>
      </div>
    </section>
  );
};

