"use client";

import { collectionSpotlight } from "@/data/migration-content";

export const CollectionSpotlight = () => {
  return (
    <section className="py-16">
      <div className="container px-4">
        <header className="mb-10 text-center space-y-2">
          <p className="text-sm uppercase tracking-[0.3em] text-muted-foreground">Shop By Collections</p>
          <h2 className="text-3xl md:text-4xl font-semibold text-foreground">Artfully Curated Capsules</h2>
        </header>

        <div className="flex flex-wrap gap-10 justify-center">
          {collectionSpotlight.map((collection) => (
            <div
              key={collection.id}
              className="flex flex-col items-center gap-4 group cursor-pointer transition-transform duration-500 hover:-translate-y-2"
            >
              <div className="relative w-32 h-32 md:w-40 md:h-40 rounded-full animate-float">
                <div className="absolute inset-0 rounded-full bg-white shadow-lg group-hover:shadow-xl transition-shadow duration-500" />
                <div className="absolute inset-2 rounded-full overflow-hidden">
                  <div className="w-full h-full transform transition-transform duration-500 group-hover:rotate-y-180 [transform-style:preserve-3d]">
                    <div className="absolute inset-0">
                      <img
                        src={collection.image}
                        alt={collection.name}
                        className="w-full h-full object-cover rounded-full"
                        loading="lazy"
                      />
                    </div>
                    <div className="absolute inset-0 bg-white flex items-center justify-center rounded-full [backface-visibility:hidden] [transform:rotateY(180deg)]">
                      <p className="text-sm font-semibold text-muted-foreground px-4 text-center">{collection.name}</p>
                    </div>
                  </div>
                </div>
              </div>
              <span
                className="text-base font-semibold"
                style={{
                  color: collection.accent,
                }}
              >
                {collection.name}
              </span>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

