import Link from "next/link";
import { blogHighlight, storeSummary } from "@/data/migration-content";
import { MessageCircle, User2 } from "lucide-react";

export const PrefooterShowcase = () => {
  return (
    <section className="bg-secondary/30 py-16">
      <div className="container px-4 grid gap-8 lg:grid-cols-[60%_40%]">
        <article className="bg-white rounded-3xl shadow-lg overflow-hidden border border-border">
          <header className="bg-foreground text-background px-6 py-4 flex items-center justify-between">
            <h3 className="text-lg font-semibold uppercase tracking-[0.3em]">Latest from the Studio</h3>
            <div className="flex gap-3 text-background/70">
              <span>Prev</span>
              <span>Next</span>
            </div>
          </header>
          <div className="grid gap-6 md:grid-cols-2 p-6">
            <div className="rounded-2xl overflow-hidden">
              <img src={blogHighlight.image} alt={blogHighlight.title} className="w-full h-72 object-cover" loading="lazy" />
            </div>
            <div className="flex flex-col gap-4">
              <p className="text-sm uppercase tracking-[0.3em] text-muted-foreground">{blogHighlight.category}</p>
              <h4 className="text-2xl font-semibold text-foreground">{blogHighlight.title}</h4>
              <p className="text-muted-foreground leading-relaxed">{blogHighlight.excerpt}</p>
              <ButtonLike href={blogHighlight.href}>{blogHighlight.ctaLabel}</ButtonLike>
              <footer className="border-t pt-4 flex items-center justify-between text-sm text-muted-foreground">
                <span className="inline-flex items-center gap-2">
                  <MessageCircle className="h-4 w-4" />
                  {blogHighlight.commentsLabel}
                </span>
                <span className="inline-flex items-center gap-2">
                  <User2 className="h-4 w-4" />
                  {blogHighlight.author}
                </span>
              </footer>
            </div>
          </div>
        </article>

        <aside className="space-y-6">
          <div className="bg-white rounded-3xl p-6 shadow-lg border border-border">
            <h3 className="text-lg font-semibold text-foreground uppercase tracking-[0.3em] mb-3">
              About Our Store
            </h3>
            <p className="text-muted-foreground leading-relaxed">{storeSummary.summary}</p>
          </div>
          <div className="bg-white rounded-3xl p-6 shadow-lg border border-border space-y-4">
            <h4 className="text-lg font-semibold">Get Connected</h4>
            <p className="text-sm text-muted-foreground">Like, share, or follow for exclusive info.</p>
            <div className="flex flex-wrap gap-3">
              {storeSummary.socials.map((social) => (
                <Link
                  key={social.id}
                  href={social.href}
                  target="_blank"
                  className="px-4 py-2 rounded-full border border-border text-sm hover:bg-primary hover:text-primary-foreground transition-colors"
                >
                  {social.label}
                </Link>
              ))}
            </div>
          </div>
        </aside>
      </div>
    </section>
  );
};

const ButtonLike = ({ href, children }: { href: string; children: React.ReactNode }) => {
  return (
    <Link
      href={href}
      className="inline-flex items-center justify-center rounded-full border border-foreground px-6 py-2 text-sm font-medium uppercase tracking-[0.3em] hover:bg-foreground hover:text-background transition-colors w-fit"
    >
      {children}
    </Link>
  );
};

