"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { heroSlides } from "@/data/migration-content";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  Carousel,
  CarouselApi,
  CarouselContent,
  CarouselItem,
} from "@/components/ui/carousel";
import { cn } from "@/lib/utils";
import { ArrowRight } from "lucide-react";

export const HeroShowcase = () => {
  const [api, setApi] = useState<CarouselApi | null>(null);
  const [current, setCurrent] = useState(0);

  useEffect(() => {
    if (!api) {
      return;
    }
    const handleSelect = () => setCurrent(api.selectedScrollSnap());
    api.on("select", handleSelect);

    const id = setInterval(() => {
      api.scrollNext();
    }, 6000);

    return () => {
      api.off("select", handleSelect);
      clearInterval(id);
    };
  }, [api]);

  return (
    <section className="py-10">
      <div className="container px-4">
        <Carousel
          opts={{
            align: "start",
            loop: true,
          }}
          setApi={setApi}
        >
          <CarouselContent>
            {heroSlides.map((slide) => (
              <CarouselItem key={slide.id}>
                <article className="relative h-[540px] rounded-[36px] overflow-hidden shadow-2xl">
                  <div className="absolute inset-0">
                    <img src={slide.image} alt={slide.title} className="w-full h-full object-cover animate-slow-zoom" />
                  </div>
                  <div className="absolute inset-0 bg-gradient-to-r from-black/70 via-black/50 to-black/20" />
                  <div className="relative z-10 h-full flex flex-col xl:flex-row">
                    <div className="hidden xl:flex flex-col justify-between w-32 px-6 py-12">
                      {slide.accentPalette.left.map((color, index) => (
                        <div
                          key={`${slide.id}-left-${color}-${index}`}
                          className="flex-1 rounded-full mb-3 last:mb-0 animate-fade-in"
                          style={{ backgroundColor: color, animationDelay: `${index * 100}ms` }}
                        />
                      ))}
                    </div>
                    <div className="flex-1 flex flex-col justify-center px-8 md:px-16 py-12 text-white space-y-6">
                      <div className="space-y-4 animate-slide-up">
                        <Badge className="bg-secondary text-secondary-foreground/90">{slide.eyebrow}</Badge>
                        <h1 className="text-4xl md:text-5xl font-semibold leading-tight">{slide.title}</h1>
                      </div>
                      <p className="text-lg text-white/80 max-w-2xl animate-fade-in">{slide.description}</p>
                      <div className="flex flex-col sm:flex-row gap-4">
                        <Button asChild size="lg" variant="premium" className="text-base tracking-tight shadow-lg hover:-translate-y-0.5 transition">
                          <Link href={slide.cta.href}>
                            {slide.cta.label}
                            <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                        </Button>
                        <Button
                          asChild
                          size="lg"
                          variant="outline"
                          className="border-white/60 text-white bg-white/10 hover:bg-white/20"
                        >
                          <Link href={slide.secondaryCta.href}>{slide.secondaryCta.label}</Link>
                        </Button>
                      </div>
                      <div className="flex items-center gap-3 text-sm uppercase tracking-[0.3em] text-white/70">
                        <span className="text-4xl font-semibold text-white">{slide.stats.value}</span>
                        <span>{slide.stats.label}</span>
                      </div>
                    </div>
                    <div className="hidden xl:flex flex-col justify-between w-32 px-6 py-12">
                      {[...slide.accentPalette.right].reverse().map((color, index) => (
                        <div
                          key={`${slide.id}-right-${color}-${index}`}
                          className="flex-1 rounded-full mb-3 last:mb-0 animate-fade-in"
                          style={{ backgroundColor: color, animationDelay: `${index * 120}ms` }}
                        />
                      ))}
                    </div>
                  </div>
                </article>
              </CarouselItem>
            ))}
          </CarouselContent>
        </Carousel>

        <div className="mt-6 flex items-center justify-between flex-wrap gap-4">
          <p className="text-sm uppercase tracking-[0.3em] text-muted-foreground">
            Crafted in Dhaka · Ships Worldwide
          </p>
          <div className="flex items-center gap-2">
            {heroSlides.map((slide, index) => (
              <button
                key={slide.id}
                type="button"
                onClick={() => api?.scrollTo(index)}
                className={cn(
                  "h-2 rounded-full transition-all duration-300",
                  current === index ? "w-12 bg-foreground" : "w-6 bg-muted",
                )}
                aria-label={`Go to slide ${index + 1}`}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

