"use client";

import { newsletterContent } from "@/data/migration-content";
import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";

export const NewsletterCta = () => {
  const [email, setEmail] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!email) {
      toast.error("Please enter an email address");
      return;
    }
    setIsSubmitting(true);
    setTimeout(() => {
      toast.success("You are on the list! 🎉");
      setIsSubmitting(false);
      setEmail("");
    }, 800);
  };

  return (
    <section className="py-16">
      <div className="container px-4">
        <div className="rounded-3xl bg-gradient-to-br from-purple-800 to-indigo-700 text-white p-10 shadow-2xl">
          <div className="flex flex-col gap-6 lg:flex-row lg:items-center justify-between">
            <div className="space-y-3 max-w-xl">
              <p className="text-sm uppercase tracking-[0.3em] text-white/70">Newsletter</p>
              <h2 className="text-3xl md:text-4xl font-semibold">{newsletterContent.title}</h2>
              <p className="text-white/80">{newsletterContent.description}</p>
            </div>
            <form onSubmit={handleSubmit} className="flex flex-col sm:flex-row gap-3 w-full lg:w-auto">
              <Input
                type="email"
                value={email}
                onChange={(event) => setEmail(event.target.value)}
                placeholder={newsletterContent.placeholder}
                className="bg-white text-foreground placeholder:text-muted-foreground h-12 rounded-full px-6"
                required
              />
              <Button
                type="submit"
                className="h-12 rounded-full px-8 bg-primary text-primary-foreground font-semibold"
                disabled={isSubmitting}
              >
                {isSubmitting ? "Joining..." : newsletterContent.cta}
              </Button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

