import { infoHighlights } from "@/data/migration-content";

export const InfoHighlights = () => {
  return (
    <section className="bg-cream/80 py-12">
      <div className="container px-4">
        <div className="grid gap-8 md:grid-cols-3">
          {infoHighlights.map((highlight) => {
            const Icon = highlight.icon;
            return (
              <article
                key={highlight.id}
                className="rounded-3xl bg-white shadow-sm border border-border/60 p-6 flex gap-4 items-start hover:shadow-lg transition-shadow"
              >
                <div className="h-14 w-14 rounded-2xl bg-secondary flex items-center justify-center shrink-0">
                  <Icon className="h-6 w-6 text-secondary-foreground" />
                </div>
                <div>
                  <h3 className="text-xl font-semibold text-foreground mb-2">{highlight.title}</h3>
                  <p className="text-sm text-muted-foreground leading-relaxed">{highlight.description}</p>
                </div>
              </article>
            );
          })}
        </div>
      </div>
    </section>
  );
};

