import { categorySpotlightTiles, floatingFacts } from "@/data/migration-content";

export const CategorySpotlight = () => {
  return (
    <section className="py-16">
      <div className="container px-4">
        <div className="grid gap-8 md:grid-cols-[0.8fr_1fr] items-stretch">
          <article className="rounded-3xl bg-gradient-to-br from-primary/90 to-primary/70 text-primary-foreground p-8 relative overflow-hidden animate-float">
            <div className="absolute inset-0 opacity-10 bg-[radial-gradient(circle_at_top,_rgba(255,255,255,0.35),_transparent_45%)]" />
            <div className="relative space-y-4">
              <p className="text-sm uppercase tracking-[0.3em]">Our Top Category</p>
              <h3 className="text-3xl font-semibold">Curated edits across bedding, bath, and lounge.</h3>
              <p className="text-primary-foreground/80">
                Insider picks from our studio. Updated weekly with trend-forward palettes and globally sourced fabrics.
              </p>
            </div>
            <div className="mt-8 grid grid-cols-2 gap-4">
              {floatingFacts.slice(0, 4).map((fact) => {
                const Icon = fact.icon;
                return (
                  <div key={fact.id} className="bg-white/10 rounded-2xl p-4 backdrop-blur-sm space-y-1">
                    <Icon className="h-5 w-5" />
                    <p className="text-xs uppercase tracking-[0.2em]">{fact.label}</p>
                    <p className="text-lg font-semibold">{fact.value}</p>
                  </div>
                );
              })}
            </div>
          </article>

          <div className="grid gap-6 md:grid-cols-3">
            {categorySpotlightTiles.map((tile) => (
              <article
                key={tile.id}
                className="rounded-2xl overflow-hidden shadow-sm border bg-white hover:shadow-xl transition-all duration-500 hover:-translate-y-2"
              >
                <div className="overflow-hidden h-48">
                  <img src={tile.image} alt={tile.name} className="w-full h-full object-cover transform hover:scale-110 transition-transform duration-700" loading="lazy" />
                </div>
                <div className="px-4 py-3 text-center">
                  <h3 className="font-semibold text-lg text-foreground">{tile.name}</h3>
                </div>
              </article>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

